"use client"

import { useEffect, useState, useCallback } from "react"
import { toast } from "sonner"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import {
    Calendar, MapPin, Clock, MoreVertical, Pencil, Trash2, XCircle, CheckCircle2
} from "lucide-react"
import { format } from "date-fns"
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuSeparator,
    DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { EditBookingModal } from "@/components/bookings/EditBookingModal"

interface Booking {
    id: string
    activityTitle: string
    date: string
    status: string
    totalPrice: number
    guests: number
    pickupLocation?: string
    imageUrl?: string | null
    createdAt: string
}

export function MyBookings() {
    const [bookings, setBookings] = useState<Booking[]>([])
    const [loading, setLoading] = useState(true)
    const [activeTab, setActiveTab] = useState<'upcoming' | 'past'>('upcoming')

    // Edit Modal State
    const [isEditModalOpen, setIsEditModalOpen] = useState(false)
    const [editingBookingId, setEditingBookingId] = useState<string | null>(null)

    const fetchBookings = useCallback(async () => {
        setLoading(true)
        try {
            const res = await fetch("/api/my-bookings")
            const data = await res.json()
            if (Array.isArray(data)) {
                setBookings(data)
            }
        } catch (err) {
            console.error("Failed to fetch bookings", err)
            toast.error("Failed to load bookings")
        } finally {
            setLoading(false)
        }
    }, [])

    useEffect(() => {
        fetchBookings()
    }, [fetchBookings])

    const handleEdit = (id: string) => {
        setEditingBookingId(id)
        setIsEditModalOpen(true)
    }

    const handleCancel = async (id: string) => {
        if (!confirm("Are you sure you want to cancel this booking? This action cannot be undone.")) return;

        try {
            const res = await fetch(`/api/bookings/${id}`, {
                method: 'DELETE',
            })

            if (!res.ok) throw new Error("Cancellation failed")

            toast.success("Booking cancelled successfully")
            fetchBookings() // Refresh list
        } catch (error) {
            console.error("Cancel error", error)
            toast.error("Failed to cancel booking")
        }
    }

    const upcomingBookings = bookings.filter(
        (b) => new Date(b.date) >= new Date() && b.status !== "CANCELLED"
    )
    const pastBookings = bookings.filter(
        (b) => new Date(b.date) < new Date() || b.status === "CANCELLED"
    )

    if (loading && bookings.length === 0) {
        return <div className="text-center py-8">Loading bookings...</div>
    }

    return (
        <Card className="h-full border-0 shadow-none bg-transparent">
            <CardHeader className="px-0 pt-0">
                <CardTitle className="text-2xl">My Bookings</CardTitle>
                <CardDescription>Manage your upcoming trips and view past history</CardDescription>
            </CardHeader>
            <CardContent className="px-0">
                <div className="w-full">
                    <div className="flex gap-4 mb-6 border-b pb-1">
                        <button
                            onClick={() => setActiveTab('upcoming')}
                            className={`px-2 py-2 text-sm font-medium border-b-2 transition-colors ${activeTab === 'upcoming'
                                ? 'border-primary text-primary'
                                : 'border-transparent text-muted-foreground hover:text-foreground'
                                }`}
                        >
                            Upcoming ({upcomingBookings.length})
                        </button>
                        <button
                            onClick={() => setActiveTab('past')}
                            className={`px-2 py-2 text-sm font-medium border-b-2 transition-colors ${activeTab === 'past'
                                ? 'border-primary text-primary'
                                : 'border-transparent text-muted-foreground hover:text-foreground'
                                }`}
                        >
                            Past / Cancelled
                        </button>
                    </div>

                    <div className="space-y-4 min-h-[300px]">
                        {activeTab === 'upcoming' ? (
                            upcomingBookings.length === 0 ? (
                                <p className="text-muted-foreground text-center py-12 border dashed rounded-xl">No upcoming bookings found.</p>
                            ) : (
                                upcomingBookings.map((booking) => (
                                    <BookingItem
                                        key={booking.id}
                                        booking={booking}
                                        onEdit={handleEdit}
                                        onCancel={handleCancel}
                                    />
                                ))
                            )
                        ) : (
                            pastBookings.length === 0 ? (
                                <p className="text-muted-foreground text-center py-12 border dashed rounded-xl">No past bookings found.</p>
                            ) : (
                                pastBookings.map((booking) => (
                                    <BookingItem
                                        key={booking.id}
                                        booking={booking}
                                        isPast
                                        // No actions usually allowed for past bookings, but maybe View Details?
                                        // For now, let's enable View/Edit if not cancelled, just in case.
                                        // Allow edit only if within 5 hours
                                        onEdit={booking.status !== 'CANCELLED' && (new Date().getTime() - new Date(booking.createdAt).getTime() < 5 * 60 * 60 * 1000) ? handleEdit : undefined}
                                        onCancel={booking.status !== 'CANCELLED' && new Date(booking.date) > new Date() ? handleCancel : undefined}
                                    />
                                ))
                            )
                        )}
                    </div>
                </div>

                <EditBookingModal
                    isOpen={isEditModalOpen}
                    onClose={() => setIsEditModalOpen(false)}
                    bookingId={editingBookingId}
                    onSuccess={fetchBookings}
                />
            </CardContent>
        </Card>
    )
}

function BookingItem({
    booking,
    onEdit,
    onCancel,
    isPast
}: {
    booking: Booking,
    onEdit?: (id: string) => void,
    onCancel?: (id: string) => void,
    isPast?: boolean
}) {
    const statusColor =
        booking.status === "CONFIRMED" ? "bg-green-100 text-green-800 border-green-200" :
            booking.status === "PENDING" ? "bg-yellow-100 text-yellow-800 border-yellow-200" :
                booking.status === "UNPROCESSED" ? "bg-blue-100 text-blue-800 border-blue-200" :
                    "bg-red-100 text-red-800 border-red-200"

    return (
        <div className="group relative border rounded-xl p-4 flex flex-col md:flex-row items-start gap-4 bg-card hover:shadow-md transition-all duration-200">
            {/* Image Thumbnail */}
            <div className="relative h-24 w-24 md:h-28 md:w-28 flex-shrink-0 rounded-lg overflow-hidden bg-muted">
                {booking.imageUrl ? (
                    <img
                        src={booking.imageUrl}
                        alt={booking.activityTitle}
                        className="object-cover w-full h-full transform group-hover:scale-105 transition-transform duration-500"
                    />
                ) : (
                    <div className="flex items-center justify-center h-full text-muted-foreground bg-muted/50">
                        <Calendar className="h-8 w-8 opacity-50" />
                    </div>
                )}
            </div>

            <div className="flex-grow space-y-3 w-full">
                <div className="flex justify-between items-start">
                    <div>
                        <h4 className="font-bold text-lg leading-tight text-foreground/90">{booking.activityTitle}</h4>
                        <div className="flex items-center gap-2 mt-1">
                            <Badge variant="outline" className={statusColor}>
                                {booking.status === 'UNPROCESSED' ? 'Awaiting Confirmation' : booking.status}
                            </Badge>
                        </div>
                    </div>

                    {/* Actions Menu */}
                    {(onEdit || onCancel) && (
                        <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                                <Button variant="ghost" size="icon" className="h-8 w-8 -mr-2 text-muted-foreground hover:text-foreground">
                                    <MoreVertical className="h-4 w-4" />
                                    <span className="sr-only">Open menu</span>
                                </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                                {onEdit && (
                                    <DropdownMenuItem onClick={() => onEdit(booking.id)}>
                                        <Pencil className="mr-2 h-4 w-4" /> Edit Details
                                    </DropdownMenuItem>
                                )}
                                {onCancel && (
                                    <>
                                        <DropdownMenuSeparator />
                                        <DropdownMenuItem onClick={() => onCancel(booking.id)} className="text-red-600 focus:text-red-600 focus:bg-red-50">
                                            <Trash2 className="mr-2 h-4 w-4" /> Cancel Booking
                                        </DropdownMenuItem>
                                    </>
                                )}
                            </DropdownMenuContent>
                        </DropdownMenu>
                    )}
                </div>

                <div className="grid grid-cols-2 lg:flex lg:flex-wrap gap-x-6 gap-y-2 text-sm text-muted-foreground">
                    <div className="flex items-center gap-2">
                        <Calendar className="h-4 w-4 text-primary/70" />
                        {format(new Date(booking.date), "PPP")}
                    </div>
                    <div className="flex items-center gap-2">
                        <Clock className="h-4 w-4 text-primary/70" />
                        {booking.guests} Guests
                    </div>
                </div>

                {booking.pickupLocation && (
                    <div className="text-sm text-muted-foreground flex items-start gap-2 max-w-md">
                        <MapPin className="h-4 w-4 mt-0.5 text-primary/70 flex-shrink-0" />
                        <span className="truncate">{booking.pickupLocation}</span>
                    </div>
                )}

                <div className="pt-2 flex justify-end border-t border-border/50 mt-2">
                    <span className="font-bold text-lg text-foreground">{booking.totalPrice}</span>
                </div>
            </div>
        </div>
    )
}
